












































	export default {
		data() {
			return {
				bannerList: [],
				indicatorDots: true,
				autoplay: true,
				interval: 5000,
				duration: 1000,
				
				typeFixed: false,
				typeList: [
					'全部'
				],
				typeIndex: 0,
				
				proList: []
			}
		},
		onLoad(e) {
			uni.request({
				url: this.$requestUrl+'get_pro_info',
				method: 'GET',
				success: res => {
					this.typeList = res.data.data.type;
                    this.bannerList = res.data.data.banner;
				},
				fail: () => {}
			});
			this.getListInfo();
		},
		methods: {
			getListInfo(type='') {
				uni.showLoading();
				uni.request({
					url: this.$requestUrl+'get_pro_list',
					method: 'GET',
					data: {
						type: type
					},
					success: res => {
						this.proList = res.data.data;
					},
					fail: () => {},
					complete: () => {
						uni.hideLoading();
					}
				});
			},
			toDetail(e) {
				var proId = e.currentTarget.dataset.id;
				uni.navigateTo({
					url: '../pro_detail/pro_detail?proid='+proId
				});
			},
			typeChange(e) {
				this.typeIndex = e.detail.value;
				let type = this.typeList[e.detail.value];
				this.getListInfo(type);
			},
			scroll: function(e) {
				let scrollTop = e.detail.scrollTop;
				if (scrollTop >= 150) {
					this.typeFixed = true;
				} else {
					this.typeFixed = false;
				}
			}
		},
		onShareAppMessage() {
			return {
				title: '鼎盛润泽--产品展示',
				path: '/pages/pro/pro'
			}
		}
	}



